/* ==========================================================================
   Slider

   Default Mendix Slider Widget.
========================================================================== */

.widget-slider {
    margin-bottom: 15px;
    padding: 5px 10px;

    .rc-slider-handle,
    .rc-slider-dot-active {
        border-color: #dddddd;
    }

    .rc-slider.rc-slider-with-marks {
        padding-bottom: 25px;
    }

    &.has-error {
        .rc-slider-track,
        .rc-slider-rail {
            background-color: $brand-danger;
        }
    }
}
