/* ==========================================================================
   Layout Grid

   Default Bootstrap containers
========================================================================== */
.mx-layoutgrid {
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    padding-right: $gutter-size;
    padding-left: $gutter-size;
}

// Row
.row {
    display: flex;
    flex-wrap: wrap;
    margin-right: -$gutter-size;
    margin-left: -$gutter-size;

    &::before,
    &::after {
        content: normal;
    }
}

.no-gutters {
    margin-right: 0;
    margin-left: 0;
}

.no-gutters > .col,
.no-gutters > [class*="col-"] {
    padding-right: 0;
    padding-left: 0;
}

